// * 
//-----------------
$path:      '../images/new/';
$font_path: './new_fonts/';



$screen: (
  "pc_width":          1280px,
  "pc_max_width":      1920px,
  "pc_margin":         40px, //1280 
  "pc_max_margin":     50px, //1280 
  "mobile_width":      100%,
  "mobile_max_width":  100%,
