/**
* Layout
* ------------------
*/
html, body {
  min-height: 100%;
  height: 100%;
}

#wrap {
  position: relative;
  min-height: 100%;
  min-width: 100%;
}

#header {
  z-index: 500;
}

#quick {
  z-index: 400;
}

#content {
  min-height: 100%;
  padding-top: 52px;
  overflow-x: hidden;
}
#content .content_wrap {
  position: relative;
  min-height: 400px;
  padding-bottom: calc(90px + 64px);
}
#content .content_wrap.no_footer {
  padding-bottom: 64px;
}
#content .content_footer {
  z-index: 209;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
#content .content_footer.fix {
  position: fixed;
}

.content_footer.fix,
.modal_footer.fix {
  padding: 0 20px 34px 20px;
}
.content_footer.fix:before,
.modal_footer.fix:before {
  content: "";
  position: absolute;
  top: -16px;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.4) 0%, #FFFFFF 19.79%);
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
}

#footer {
  z-index: 300;
}

html.mobile .tag_scroll,
html.mobile .tab_scroll {
  -ms-overflow-style: none;
  scrollbar-width: none;
  scrollbar-height: none;
}
html.mobile .tag_scroll::-webkit-scrollbar,
html.mobile .tab_scroll::-webkit-scrollbar {
  display: none;
}

