/**
* Layout.content
* ------------------
* section & comp
* PC/MO 
*/
hr {
  border: none;
}

.hr {
  position: relative;
  height: 12px;
  padding-top: 12px;
}
.hr:before {
  content: "";
  position: absolute;
  left: -20px;
  right: -20px;
  top: 0;
  height: 12px;
  background: #F9F9FB;
}
.hr.mtb {
  margin-top: 32px;
  margin-bottom: 32px;
  padding-top: 0;
}
.hr.mtb_sm {
  margin-top: 16px;
  margin-bottom: 16px;
  padding-top: 0;
}
.hr + .comp {
  margin-top: 0;
}
.hr.section, .hr.comp {
  height: auto;
}
.hr.section:before, .hr.comp:before {
  top: 0;
}
.hr.section.mtb, .hr.comp.mtb {
  margin-top: 76px;
}
.hr.section.mtb:before, .hr.comp.mtb:before {
  top: -44px;
}
.hr.section:last-child, .hr.comp:last-child {
  margin-bottom: 0;
}

.hr2 {
  position: relative;
  height: 2px;
  padding-top: 2px;
}
.hr2:before {
  content: "";
  position: absolute;
  left: -20px;
  right: -20px;
  top: 0;
  height: 2px;
  background: #F9F9FB;
}
.hr2.mtb {
  margin-top: 32px;
  margin-bottom: 32px;
  padding-top: 0;
}
.hr2.mtb_sm {
  margin-top: 16px;
  margin-bottom: 16px;
  padding-top: 0;
}
.hr2 + .comp {
  margin-top: 0;
}
.hr2.section, .hr2.comp {
  height: auto;
}
.hr2.section:before, .hr2.comp:before {
  top: 0;
}
.hr2.section.mtb, .hr2.comp.mtb {
  margin-top: 66px;
}
.hr2.section.mtb:before, .hr2.comp.mtb:before {
  top: -34px;
}
.hr2.section:last-child, .hr2.comp:last-child {
  margin-bottom: 0;
}

.section_group + .section_group {
  margin-top: 100px;
}

.section {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  margin: 0 auto;
  padding-top: 0;
}
.section::after {
  display: block;
  content: "";
  clear: both;
}
.section.sm {
  width: 550px;
}
.section.full {
  width: auto;
}
.section + .section {
  padding-top: 0;
  margin-top: 32px;
}

.comp {
  box-sizing: border-box;
  position: relative;
  width: calc(100% - 40px);
  margin: 32px auto 0;
}
.comp::after {
  display: block;
  content: "";
  clear: both;
}
.comp:first-child {
  margin-top: 0;
}
.comp.full {
  width: auto;
}
.comp.border_top {
  margin-top: 32px;
  border-top: 1px solid #EFEFF1;
}
.comp.border_bottom {
  border-bottom: 1px solid #EFEFF1;
}

.section.full .comp.grid1 {
  width: auto;
  min-width: calc(100% - 40px);
  max-width: calc(100% - 40px);
  margin-left: auto;
  margin-right: auto;
}

.section > .comp:first-child {
  margin-top: 0;
}

.modal.bottom .comp {
  width: calc(100% - 48px);
}

.tab + .section {
  padding-top: 0;
}

.section > .tab_content {
  margin-top: 0;
}

.modal .section {
  padding-top: 0;
}

.grid1 {
  width: 100%;
}

.grid2 {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 20px;
  justify-content: space-between;
}
.grid2 > * {
  width: calc( 50% - 20px );
}
.grid2.no_pad {
  gap: 0;
}
.grid2.no_pad > * {
  width: 50%;
}

.grid_sm2 {
  display: flex;
  flex-wrap: wrap;
  gap: 8px 4px;
  justify-content: space-between;
}
.grid_sm2 > * {
  width: calc( 50% - 4px );
}
.grid_sm2.no_pad {
  gap: 0;
}
.grid_sm2.no_pad > * {
  width: 50%;
}

.fl_grid2::after {
  display: block;
  content: "";
  clear: both;
}
.fl_grid2 > * {
  box-sizing: border-box;
  margin-left: 0 !important;
  margin-right: 0 !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
  float: left;
  width: calc( 50% - 24px );
  margin-right: calc((100% - ( ((100% / 2) - 24px) * 2)) / (2 - 1)) !important;
  margin-top: 20px;
}
.fl_grid2 > * {
  width: calc( 50% - 12px );
  float: left;
  margin-right: 0 !important;
}
.fl_grid2 > *:nth-child(2n) {
  float: right;
}
.fl_grid2 > *:nth-of-type(1) {
  margin-top: 0;
}
.fl_grid2 > *:nth-of-type(2) {
  margin-top: 0;
}

.grid3 {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 20px;
  justify-content: space-between;
}
.grid3 > * {
  width: calc( 33.3333333333% - 20px );
}
.grid3.no_pad {
  gap: 0;
}
.grid3.no_pad > * {
  width: 33.3333333333%;
}

.grid_sm3 {
  display: flex;
  flex-wrap: wrap;
  gap: 8px 4px;
  justify-content: space-between;
}
.grid_sm3 > * {
  width: calc( 33.3333333333% - 4px );
}
.grid_sm3.no_pad {
  gap: 0;
}
.grid_sm3.no_pad > * {
  width: 33.3333333333%;
}

.fl_grid3::after {
  display: block;
  content: "";
  clear: both;
}
.fl_grid3 > * {
  box-sizing: border-box;
  margin-left: 0 !important;
  margin-right: 0 !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
  float: left;
  width: calc( 33.3333333333% - 24px );
  margin-right: calc((100% - ( ((100% / 3) - 24px) * 3)) / (3 - 1)) !important;
  margin-top: 20px;
}
.fl_grid3 > *:nth-of-type(3n) {
  float: right;
  margin-right: 0 !important;
