/**
* Layout.popup
* ------------------
*/
.modal .modal_content {
  width: calc(100% - 40px);
}
.modal.full .modal_content, .modal.bottom .modal_content, .modal.bar .modal_content {
  width: 100%;
}
.modal.alert .modal_content {
  width: calc(100% - 40px);
  max-width: 300px;
}

.modal {
  z-index: 8010;
}

.modal.alert {
  z-index: 8300;
}

.modal_open {
  overflow-x: hidden;
  overflow-y: hidden !important;
}
.modal_open #wrap {
  z-index: 0;
}

.modal.fade {
  transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1) 0s;
  opacity: 0;
}

.modal.show {
  opacity: 1;
}

.modal.full.fade {
  transition: none;
  opacity: 0;
}

.modal.full.show {
  opacity: 1;
}

.modal.drag.fade,
.modal.bottom.fade {
  transform: translate3d(0, 100%, 0);
  opacity: 0;
}

.modal.drag.show,
.modal.bottom.show {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.modal_backdrop {
  position: fixed;
  z-index: 8000;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000;
  opacity: 0.5;
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  outline: 0;
}
.modal .modal_dialog {
  position: relative;
  width: auto;
  height: auto;
  min-height: 100%;
  margin: 0 auto;
  pointer-events: none;
  -webkit-appearance: none;
  vertical-align: center;
  display: flex;
  align-items: center;
}
.modal .modal_content {
  position: relative;
  margin: 0 auto;
  border-radius: 24px;
  background: #fff;
  background-clip: padding-box;
