.accordion {
  border: 1px solid #ccc;
  border-width: 1px 0;
}
.accordion.full {
  margin-left: -20px;
  margin-right: -20px;
}
.modal.bottom .accordion.full {
  margin-left: -24px;
  margin-right: -24px;
}
.accordion .item {
  border-top: 1px solid #ccc;
}
.accordion .item:first-child {
  border-top: none;
}
.accordion .item .fold_header {
  position: relative;
}
.accordion .item .fold_header a {
  display: table;
  table-layout: fixed;
  width: 100%;
  padding: 24px 20px;
  color: #fff;
}
.accordion .item .fold_header .title {
  width: calc(100% - 30px);
}
.accordion .item .fold_header .title::after {
  display: block;
  content: "";
  clear: both;
}
.accordion .item .fold_header .title strong {
  color: #000;
  font-weight: 500;
  vertical-align: middle;
  word-break: break-word;
}
.accordion .item .fold_header .title span {
  float: right;
  color: #000;
  vertical-align: middle;
  word-break: break-word;
  text-align: right;
}
.accordion .item .fold_header .icon_angle {
  position: absolute;
  top: 24px;
  right: 20px;
  width: 24px;
  height: 24px;
  background: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 10L12 16L18 10' stroke='black' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") center center no-repeat;
  background-size: 24px 24px;
  transform: rotate(0);
  vertical-align: top;
}
.accordion .item .fold_header .icon_angle span {
  position: absolute;
  display: inline-block;
  font-size: 1px;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  visibility: hidden;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}
.accordion .item .fold_body {
  display: none;
  padding: 2px 20px 27px;
}
.accordion .item.on .fold_header a {
  border-bottom: none;
}
.accordion .item.on .fold_header a .icon_angle {
  transform: rotate(180deg);
}

.accordion.notice {
  border: none;
  border-top: 12px solid #F9F9FB;
}
.accordion.notice .item {
  border-top-color: #EFEFF1;
}
.accordion.notice .fold_header {
  background: #fff;
}
.accordion.notice .fold_header a {
  padding: 24px 20px;
}
.accordion.notice .fold_header .title {
