}
.accordion.notice .fold_body.uol_list, .accordion.notice .fold_body .uol_list {
  font-weight: 400;
  font-size: 13px;
  line-height: 1.5em;
  letter-spacing: -0.02em;
  color: #767678;
  color: #555557;
}
.accordion.notice .fold_body.uol_list ul li, .accordion.notice .fold_body.uol_list ol li, .accordion.notice .fold_body .uol_list ul li, .accordion.notice .fold_body .uol_list ol li {
