.accordion.notice .fold_body.uol_list ul li:first-child, .accordion.notice .fold_body.uol_list ol li:first-child, .accordion.notice .fold_body .uol_list ul li:first-child, .accordion.notice .fold_body .uol_list ol li:first-child {
  margin-top: 0;
}
.accordion.notice .fold_body.uol_list ul li ul li, .accordion.notice .fold_body.uol_list ol li ul li, .accordion.notice .fold_body .uol_list ul li ul li, .accordion.notice .fold_body .uol_list ol li ul li {
  font-weight: 400;
  font-size: 13px;
  line-height: 1.5em;
  letter-spacing: -0.02em;
  color: #767678;
  color: #767678;
}
.accordion.notice .fold_body.uol_list .nobul > li, .accordion.notice .fold_body .uol_list .nobul > li {
  padding-left: 0;
}
.accordion.notice .fold_body.uol_list .nobul > li:before, .accordion.notice .fold_body .uol_list .nobul > li:before {
