.alarm_nodata {
  margin-top: 207px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5em;
  letter-spacing: -0.04em;
  text-align: center;
  color: #767678;
}
.alarm_text {
  font-size: 14px;
  line-height: 1.5em;
  letter-spacing: -0.04em;
  color: #555557;
}

.modal.full .alarm_list {
  margin-top: 10px;
}

.cart .date {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5em;
  letter-spacing: -0.04em;
  color: #1F1F21;
}
.cart .date + .contract_anchor {
  margin-top: 16px;
}
.cart .contract_anchor {
  position: relative;
  border-radius: 16px;
  background-color: #fff;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}
.cart .contract_anchor + .contract_anchor {
  margin-top: 10px;
}
.cart .contract_title {
  margin-top: 16px;
}
.cart .contract_right .small {
  margin-top: 0;
}
.cart_name {
  font-size: 13px;
  line-height: 1.5em;
  letter-spacing: -0.04em;
  color: #F8670D;
}
.cart_item + .cart_item {
  margin-top: 32px;
  padding-top: 32px;
  border-top: 1px solid #EFEFF1;
}

