.form_list {
  margin-top: 10px;
}
.form_list .list {
  border: 0;
}
.form_list .list_item {
  position: relative;
  padding: 16px 0 16px 36px;
  border-bottom: 1px solid #efeff1;
}
.form_list .list_item .form_list_label {
  display: block;
  width: 100%;
  padding: 0;
  color: #1F1F21;
  font-size: 15px;
}
.form_list .list_item input + .form_list_label:before {
  left: auto;
  right: 0;
}
.form_list .icon {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  background-position: 50% 50%;
