@mixin risk {
	position: relative;
	flex: 1;
	width: 100%;
	height: 10px;
	margin-top: 30px;
	text-align: center;

	&.on:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		display: inline-block;
		width: 10px;
		height: 10px;
		border: 3px solid $risk1;
		border-radius: 50%;
		background: #fff;
		transform: translate(-50%, -50%);
	}
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		display: inline-block;
		width: 2px;
		height: 10px;
		background: #fff;
	}
	&:first-child {
		border-radius: 10px 0 0 10px;
		&:before {display: none;}
	}
	&:last-child {border-radius: 0 10px 10px 0;}
	span {position: absolute;top: -30px;left: 50%;transform: translateX(-50%);}
}
@mixin risk1() {
	background-color: $risk1;
	&.on {
		color: $risk1;
		&:after {border-color: $risk1;}
	}
}
@mixin risk2() {
	background-color: $risk2;
