.menu_search_cont{
  .search_results{
    margin-top: 32px;
    @include fz13_700();
    font-weight: 400;
    .point_color{
      font-weight: 500;
    }
  }
  
  .menu_search_list{
    margin-top: 10px;
    ul{
      li{
        padding: 16px 0;
        border-top: 1px solid #EFEFF1;
        &:first-child {
          border-top: 0 none;
        }
        a{
          @include fz15_400();
        }
        .location{
          display: block;
          margin-top: 4px;
          @include fz13();
        }
      }
    }
    .governing.nodata{
      p{
        font-weight: 400;
        .point_color{
          font-weight: 500;
        }
      }
    }
  }
  
}