.service{
  &_item{
    margin-top:32px;
    &:first-child{
      margin-top:0;
    }
    ul{
      margin-top:8px;
      li{
        position:relative;
        padding-left:10px;
        margin-top:5px;
        font-size:13px;
        line-height:1.5em;
        letter-spacing:-0.04em;
        color:#555557;
        &:first-child{
          margin-top:0;
        }
        &:after{
          content:'';
          position:absolute;
          top:9px;
          left:1px;
          width:2px;
          height:2px;
          border-radius:50%;
          background-color:#555557;
        }
      }
    }
  }
  &_title{
    position:relative;
    padding-left:30px;
    & > p{
      font-size:15px;
      font-weight:700;
      line-height:1.5em;
      letter-spacing:-0.02em;
      color:#1F1F21;
    }
    i{
      position:absolute;
      top:50%;
      left:0;
      width:24px;
      height:24px;
      margin-top:-12px;
      background-position:50% 50%;
      background-repeat:no-repeat;
      background-size:100% 100%;
    }
  }
  &_sub_title{
    margin-top:16px;
    font-size:14px;
    font-weight:500;
    line-height:1.5em;
    letter-spacing:-0.04em;
    color:#3F3F41;
  }
}

