    margin-top:10px;
    &:first-child{
      margin-top:0;
    }
    &.compact{
      display:flex;
      align-items:center;
      .donation_image{
        position:relative;
        width:80px;
        height:80px;
        img{
          position:absolute;
          top:0;
          bottom:0;
          left:50%;
          width:auto;
          height:100%;
          transform:translateX(-50%);
        }
      }
      .donation_contents{
        margin-top:0;
        padding-left:14px;
        width:calc(100% - 80px);
      }
      .d_day.end{
        border-color:#DFDFE1;
        color:#767678;
      }
      .donation_title{
        margin-top:6px;
        @include line_clamp(1.5em,42,2);
        font-weight:400;
      }
    }
    &.end{
      .donation_title{
        color:#767678;
      }
    }
  }
  &_image{
    overflow:hidden;
    border-radius:16px;
    img{
      width:100%;
    }
  }
  &_contents{
    margin-top:20px;
  }
  &_info{
    font-size:0;
    .d_day{
      display:inline-block;
      padding:0 6px;
      font-size:10px;
      font-weight:700;
      line-height:18px;
      vertical-align:top;
      letter-spacing:-0.04em;
      color:#F8670D;
      border:1px solid rgba(248, 103, 13, 0.4);;
      border-radius:28px;
    }
    strong{
      margin-top:1px;
      padding-left:8px;
      font-size:12px;
      line-height:1.5em;
      letter-spacing:-0.04em;
      color:#767678;
      vertical-align:top;
      @include ellipsis(calc(100% - 40px));
    }
  }
  &_title{
    margin-top:8px;
    font-size:15px;
    font-weight:500;
    line-height:1.5em;
    letter-spacing:-0.04em;
    word-break:keep-all;
    color:#1F1F21;
    &+.status{
      margin-top:20px;
    }
  }
  &_date{
    margin-top:12px;
    font-size:13px;
    line-height:1.5em;
    letter-spacing:-0.04em;
    color:#767678;
    &+.status{
      margin-top:24px;
    }
  }
  .btn_area{
    .btn{
      color:#1F1F21;
    }
  }
  &+.btn_area.more{
    .btn.more{
      margin-top:24px;
    }
  }
  &_big{
    margin-top:40px;
    padding-bottom:24px;
    border-bottom:1px solid #EFEFF1;
    .donation_contents{
      position:relative;
      margin-top:24px;
    }
    .donation_title{
      margin-top:24px;
      font-size:19px;
      font-weight:700;
      line-height:1.4em;
      letter-spacing:-0.02em;
    }
    .status .status_foot{
      margin-top:12px;
      .left{
        font-size:17px;
      }
    }
    .btn.share{
      position:absolute;
      top:-2px;
      right:0;
      min-width:0;
      width:16px;
      height:16px;
      padding:0;
      border:none;
      &:before{
        content:'';
        position:absolute;
        top:50%;
        left:50%;
        width:36px;
        height:36px;
        margin:-18px 0 0 -18px;
      }
    }
  }
  &_detail{
    margin-top:32px;
    padding-bottom:32px;
    border-bottom:1px solid #EFEFF1;
  }
  &_amount{
    @include box(gray);
    text-align:center;
    span,strong{
      display:block;
    }
    span{
      font-weight:500;
      line-height:1.5em;
      letter-spacing:-0.04em;
      color:#1F1F21; 
    }
    strong{
      margin-top:4px;
      font-size:22px;
      font-weight:700;
      line-height:1.4em;
      letter-spacing:-0.02em;
      color:#F8670D;
    }
  }
  &_slide{
    margin-top:40px;
    .swiper-slide{
      width:200px;
      border:1px solid #EFEFF1;
      a{
        display:block;
      }
      img{
        width:100%;
      }
    }
  }
}
