      }
      .content{
        margin-left:14px;
        strong,span{
          display: block;
        }
        strong{
          color:#1F1F21;
        }
        span{
          position:relative;
          margin-top:6px;
          padding-left:16px;
          font-size:11px;
          line-height:1.4em;
          letter-spacing:-0.04em;
          color:#767678;
          &:after{
            content:'';
            position:absolute;
            top:50%;
            left:0;
            width:12px;
            height:12px;
            margin-top:-6px;
            background:url($svg_icon_video_02) 50% 50% no-repeat;
            background-size:100% 100%;
          }
        }
      }
    }
  }
