.alarm{
  &_item{
    margin-top:32px;
    padding-bottom:32px;
    border-bottom:1px solid #EFEFF1;
    &:first-child{
      margin-top:0;
    }
    dt{
      font-size:14px;
      font-weight:500;
      line-height:1.5em;
      letter-spacing:-0.04em;
      color:#1F1F21;
      &+dd{
        margin-top:16px;
      }
    }
    dd{
      position:relative;
      margin-top:24px;
      padding-left:30px;
      &:after{
        content:'';
        position:absolute;
        top:0;
        left:0;
        width:24px;
        height:24px;
        background-position:50% 50%;
        background-repeat:no-repeat;
        background-size:100% 100%;
      }
      a{
        display:block;
        position:relative;
        margin-left:-30px;
        padding-left:30px;
        &:after{
          content:'';
          position:absolute;
          top:0;
          right:0;
          width:24px;
          height:24px;
          background:url($svg_arrow_right_24) 50% 50% no-repeat;
          background-size:100% 100%;
        }
      }
      label{
        display:block;
        margin-left:-30px;
        padding:0 32px 0 30px;
        &:before{
          right:0;
          left:auto;
        }
        div{
          margin-right:-32px;
        }
      }
      span{
        display:block;
        padding-top:1px;
        font-size:13px;
        line-height:1.5em;
        letter-spacing:-0.04em;
        color: #767676;
      }
      strong{
        display:block;
        margin-top:2px;
        font-size:15px;
        font-weight:500;
        line-height:1.5em;
        letter-spacing:-0.04em;
        color:#1F1F21;
      }
      div{
        overflow:hidden;
        margin-top:17px;
        border-radius:12px;
        img{
          width:100%;
        }
      }
      &.new{
        strong{
          display:inline-block;
          &:after{
            content:'';
            display:inline-block;
            width:14px;
            height:14px;
            margin-left:4px;
            vertical-align:middle;
            background:url($svg_icon_new) 50% 50% no-repeat;
            background-size:100% 100%;
          }
        }
      }
      &.daily{
        &:after{
          background-image:url($svg_icon_alarm01);
        }
      }
      &.contract{
        &:after{
          background-image:url($svg_icon_alarm02);
        }
      }
      &.compensation{
        &:after{
          background-image:url($svg_icon_alarm03);
        }
      }
      &.benefits{
        &:after{
          background-image:url($svg_icon_alarm04);
        }
      }
      &.event{
        &:after{
          background-image:url($svg_icon_alarm05);
        }
      }
    }
  }
  &_nodata{
    margin-top:207px;
    font-size:14px;
    font-weight:500;
    line-height:1.5em;
    letter-spacing:-0.04em;
    text-align:center;
